<template>
  <div class="login">
    <!--  -->
    <el-card class="box-card">
      <h1 style="margin: 0 0 14px 90px"></h1>
      <!--  or  -->
      <el-radio-group v-model="labelPosition" class="radioGroup" size="small">
        <el-radio label="login"></el-radio>
        <el-radio label="signIn"></el-radio>
      </el-radio-group>

      <!-- user -->
      <el-form label-position="right" label-width="80px" :model="user">
        <el-form-item label="" prop="name" :rules="[{ required: true, message: '', trigger: 'blur' }]">
          <el-input v-model="user.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password" :rules="[{ required: true, message: '', trigger: 'blur' }]">
          <el-input type="password" v-model="user.password" show-password></el-input>
        </el-form-item>
        <el-form-item v-if="labelPosition === 'signIn'" label="" prop="checkPassword"
          :rules="[{ required: true, message: '', trigger: 'blur' }]">
          <el-input type="password" v-model="user.checkPassword" show-password></el-input>
        </el-form-item>
        <!---->
        <el-form-item class="button">
          <el-button v-if="labelPosition === 'login'" type="warning" @click="login"
            :disabled="user.name === '' || user.password === ''" round>
          </el-button>
          <el-button v-if="labelPosition === 'signIn'" type="warning" @click="login"
            :disabled="user.name === '' || user.password === '' || user.checkPassword === ''" round>
          </el-button>
          <el-button @click="resetForm" round></el-button>
          
          
          <!-- <div style="float: right;" >
            <router-link class="link-type" :to="'/register'"></router-link>
          </div> -->
        </el-form-item>
     

      </el-form>
    </el-card>
  </div>
</template>
 
<script>
export default {
  data() {
    return {
      labelPosition: 'login',  // 
      // 
      user: {
        name: '',
        password: '',
        checkPassword: '',
      }
    }
  },
  methods: {
    // 
    login() {
      this.$router.push('project')
    },
    // 
    signIn() {
      if (this.user.checkPassword !== this.user.password) {
        this.$message.error("!")
      }
    },
    // 
    resetForm() {
      this.user.name = ""
      this.user.password = ""
      this.user.checkPassword = ""
    }
  }
}
</script>
 
<style>
.login {
  width: 100%;
  height: 100%;
  /*position: fixed;*/
  /*background-image: url(~@/assets/img/login.png);*/
  /*background-repeat: no-repeat;*/
  /*background-size: cover;*/
}

.box-card {
  width: 480px;
  border: 1px solid #1f808c;
  position: fixed;
  top: 50%;
  left: 50%;
  /* margin: 200px 0 0 530px; */
  transform: translate(-50%, -50%)
}

.radioGroup {
  margin: 0 0 10px 150px;
}

.button {
  margin: 0 0 0 50px;
}
</style>